import { Injectable , EventEmitter} from '@angular/core';
import { LoggingService } from './LoggingService.service';
import { Persona } from './persona.model';

@Injectable() // Inyectamos el servicio en la clave

export class PersonaService {
  personas: Persona[] = [
    new Persona('Julia', 'Montoya'),
    new Persona('Jes√∫s', 'Nava'),
    new Persona('Oscar', 'Ortiz'),
    new Persona('Said', 'Gallegos'),
  ];

  saludar = new EventEmitter<number>();

  constructor(private loggingService: LoggingService){ // Instancia de

  }

  agregarPersona(persona: Persona){
    this.loggingService.enviaMensajeConsola('Agregamos persona: ' + persona.nombre + ' ' + persona.apellido);
    this.personas.push(persona);
  }

  encontrarPersona(index: number){
    let persona: Persona = this.personas[index];
    return persona;
  }

  modificarPersona(index: number, persona: Persona){
    let persona1 = this.personas[index];
    persona1.nombre = persona.nombre;
    persona1.apellido = persona.apellido;
  }
  eliminarPersona(index: number){
    this.personas.splice(index, 1);
  }
}
